button.outliness {
    cursor: pointer;
    outline: none;
}
button.outliness {
    position: relative;
    z-index: 3;
    background: transparent;
    color: #1172c4;
    font-size: 14px;
    border-color: #1172c4;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 10px 40px;
    text-transform: uppercase;
    transition: all 0.2s linear;
}
button.outliness:hover {
    color: black;
    background: rgba(255, 255, 255, .2);
    border-color: black;
    transition: all 0.2s linear;
}
button.outliness:active {
    border-radius: 22px;
}
button.purple-white {
    font-weight: 700;
    color: white;
    border-color: white;
    background: transparent;
}
button.purple-white:hover {
    color: black;
    background: rgba(255, 255, 255, .2);
    border-color: black;
    transition: all 0.2s linear;
}
