button.outliness {
    cursor: pointer;
    outline: none;
}
button.outliness {
    position: relative;
    z-index: 3;
    background: transparent;
    color: #1172c4;
    font-size: 14px;
    border-color: #1172c4;
    border-style: solid;
    border-width: 1px;
    border-radius: 5px;
    padding: 10px 40px;
    text-transform: uppercase;
    transition: all 0.2s linear;
}
button.outliness:hover {
    color: black;
    background: rgba(255, 255, 255, .2);
    border-color: black;
    transition: all 0.2s linear;
}
button.outliness:active {
    border-radius: 22px;
}
button.purple-white {
    font-weight: 700;
    color: white;
    border-color: white;
    background: transparent;
}
button.purple-white:hover {
    color: black;
    background: rgba(255, 255, 255, .2);
    border-color: black;
    transition: all 0.2s linear;
}

.social-container {
	 width: 400px;
	 margin: 40vh auto;
	 text-align: center;
}
 .social-icons {
	 padding: 0;
	 list-style: none;
	 margin: 1em;
}
 .social-icons li {
	 display: inline-block;
	 margin: 0.15em;
	 position: relative;
	 font-size: 1.2em;
}
 .social-icons i {
	 color: #fff;
	 position: absolute;
	 top: 21px;
	 left: 21px;
	 transition: all 265ms ease-out;
}
 .social-icons a {
	 display: inline-block;
}
 .social-icons a:before {
	 transform: scale(1);
	 -ms-transform: scale(1);
	 -webkit-transform: scale(1);
	 content: " ";
	 width: 60px;
	 height: 60px;
	 border-radius: 100%;
	 display: block;
	 background: linear-gradient(45deg, #00b5f5, #002a8f);
	 transition: all 265ms ease-out;
}
 .social-icons a:hover:before {
	 -webkit-transform: scale(0);
	         transform: scale(0);
	 transition: all 265ms ease-in;
}
 .social-icons a:hover i {
	 transform: scale(2.2);
	 -ms-transform: scale(2.2);
	 -webkit-transform: scale(2.2);
	 color: #00b5f5;
	 background: -webkit-linear-gradient(45deg, #00b5f5, #002a8f);
	 -webkit-background-clip: text;
	 -webkit-text-fill-color: transparent;
	 transition: all 265ms ease-in;
}
 
