.Offers-Class {
    /* The image used */
    background-image: url(../../assets/3.jpg);

    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;

    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

}

.Offers-Content {
    padding: 40px;
    color: black;
    /* width: 40%;
    height: 60%; */
}

.jumbotron {
    background-color: rgba(0, 0, 0, .5) !important;
}


button.outlines {
    cursor: pointer;
    outline: none;
}

button.outlines {
    position: relative;
    z-index: 3;
    background: transparent;
    color: white;
    /* font-size: 14px; */
    border-color: white;
    border-style: solid;
    /* border-width: 1px; */
    border-radius: 5px;
    padding: 5px 20px;
    background: rgba(119, 21, 21, 0.883);
    /* text-transform: uppercase; */
    /* transition: all 0.2s linear; */
}
button.outlines:hover {
    color: white;
    background: rgba(192, 183, 183, 0.698);
    border-color: black;
    transition: all 0.2s linear;
}


