button.outliness {
    cursor: pointer;
    outline: none;
}
button.outliness {
    position: relative;
    z-index: 3;
    background: rgb(172, 19, 19);
    color: white;
    font-size: 14px;
    border-color: black;
    border-style: solid;
    border-width: 1px;
    border-radius: 2px;
    padding: 10px 40px;
    text-transform: uppercase;
    transition: all 0.2s linear;
}
button.outliness:hover {
    color: white;
    background: rgba(155, 31, 31, 0.548);
    border-color: white;
    transition: all 0.2s linear;
}
button.outliness:active {
    border-radius: 22px;
}
